import {
  WebComponentOptions,
  defineWebComponent
} from '@websublime/wbs-ui-common';

import { WsLoader } from './loader';

interface WsLoaderOptions extends WebComponentOptions {
  className?: string[];
}

/**
 * @public
 */
const registerComponent = (options: WsLoaderOptions = {}, name = 'ws-loader') =>
  defineWebComponent(name, WsLoader, { className: ['ws-loader'], ...options });

declare global {
  interface HTMLElementTagNameMap {
    'ws-loader': WsLoader;
  }
}

export { registerComponent };

export { WsLoader } from './loader';
