export type Manifest = {
  name: string;
  version: string;
  scope: string;
  repo: string;
  scripts: {
    main: string;
    esm: string;
  };
  styles: Array<string>;
  docs: string;
};

export const fetchManifest = async (scope: string, name: string) => {
  const baseUrl = import.meta.env.VITE_BUCKET_URL;
  const storagePath = 'manifests';
  const environmentPath = import.meta.env.DEV ? 'develop/' : '';

  const url = `${baseUrl}/${storagePath}/${scope}/${name}/${environmentPath}manifest.json`;

  const response = await fetch(url, {
    method: 'GET'
  });

  const manifest: Manifest = await response.json();

  return response.ok ? manifest : undefined;
};
