import {
  ComponentElement,
  Ref,
  createRef,
  property,
  ref,
  uniqueID
} from '@websublime/wbs-ui-common';
import { LitElement, html } from 'lit';

import { scriptLoader } from './script';
import { version } from './version';

const metadata = {
  description: 'Loader container component',
  link: 'https://cdn.websublime.dev',
  name: 'ws-loader',
  qa: 'ws-ui-loader',
  scope: '@websublime/wbs-loader',
  version
};

/**
 * Loader component element
 */
export class WsLoader extends ComponentElement {
  static shadowRootOptions = {
    ...LitElement.shadowRootOptions,
    delegatesFocus: true
  };

  elRef: Ref<HTMLHeadingElement> = createRef();

  @property({ type: String })
  name!: string;

  @property({ type: String })
  scope!: string;

  /**
   * Element id
   */
  private identifier = uniqueID();

  get element() {
    return { element: this.elRef, registry: this.registry };
  }

  constructor() {
    super(metadata);

    const classes = this.options.className as Array<string>;

    this.classList.add(...classes);
    this.dataset.qa = metadata.qa;
  }

  connectedCallback() {
    super.connectedCallback();

    scriptLoader(this.scope, this.name);
  }

  render() {
    return html`
      <template id=${this.identifier} ${ref(this.elRef)}>
        <slot></slot>
      </template>
    `;
  }
}
